
* {
  box-sizing: border-box;
}
html, body {
  margin: 0;
  padding: 0;
}
body {
  height: 100vh;
}

.App {
  /* text-align: center; */
  min-height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.container {
  margin: auto;
  min-height: 100%;
  max-width: 900px;
  padding: 5px 15px;
}

header {
  background-color: rgb(12, 127, 153);
  padding: 20px;
  margin-bottom: 10px;
  color: white;
}
header h1 {
  text-align: center;
}

.tiles {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  text-align: center;
}
.tile {
  display: flex;
  flex-direction: column;
  margin: 10px 5px;
  max-width: 200px;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,0.2);
  text-decoration: none;
  color: rgb(40, 40, 40);
  background-color: rgb(250, 250, 250);
  transition: all 0.5s ease;
}
.tile:hover {
  background-color: rgb(250, 250, 250);
  transform: scale(1.05);
}
.tile-image img {
  width: 100%;
}
.tile-details h3 {
  margin: 5px 0;
}
.tile-footer {
  font-size: 0.9em;
  padding: 8px 2px;
  margin-top: auto;
  background-color: rgba(20, 20, 20, 0.05);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
